<template>
  <div class="index">
    <a href="https://github.com/Clearives" target="_blank" title="Clearives" class="github-corner" aria-label="View source on Github">
        <svg width="80" height="80" viewBox="0 0 250 250" style="fill:#50bfff; color:#fff; position: absolute; top: 40px; border: 0; right: 0; z-index: 33;"
        aria-hidden="true">
        <path d="M0,0 L115,115 L130,115 L142,142 L250,250 L250,0 Z"></path>
        <path d="M128.3,109.0 C113.8,99.7 119.0,89.6 119.0,89.6 C122.0,82.7 120.5,78.6 120.5,78.6 C119.2,72.0 123.4,76.3 123.4,76.3 C127.3,80.9 125.5,87.3 125.5,87.3 C122.9,97.6 130.6,101.9 134.4,103.2"
            fill="currentColor" style="transform-origin: 130px 106px;" class="octo-arm"></path>
        <path d="M115.0,115.0 C114.9,115.1 118.7,116.5 119.8,115.4 L133.7,101.6 C136.9,99.2 139.9,98.4 142.2,98.6 C133.8,88.0 127.5,74.4 143.8,58.0 C148.5,53.4 154.0,51.2 159.7,51.0 C160.3,49.4 163.2,43.6 171.4,40.1 C171.4,40.1 176.1,42.5 178.8,56.2 C183.1,58.6 187.2,61.8 190.9,65.4 C194.5,69.0 197.7,73.2 200.1,77.6 C213.8,80.2 216.3,84.9 216.3,84.9 C212.7,93.1 206.9,96.0 205.4,96.6 C205.1,102.4 203.0,107.8 198.3,112.5 C181.9,128.9 168.3,122.5 157.7,114.1 C157.9,116.9 156.7,120.9 152.7,124.9 L141.0,136.5 C139.8,137.7 141.6,141.9 141.8,141.8 Z"fill="currentColor" class="octo-body"></path>
        </svg>
    </a>
    <div class="appinfo">
      <li>appName: {{appInfo.appName}}</li>
      <li>appVersion: {{appInfo.appVersion}}</li>
      <li>platform: {{appInfo.platform}}</li>
      <li>userAgent: {{appInfo.userAgent}}</li>
    </div>
    <div>
      <img class='gif' :src="gif" >
      <img class='gif rotate' :src="gif" >
    </div>
  </div>
</template>

<script>
import gif from '../../assets/0.gif';
export default {
  name: 'dashboard-index',
  data() {
    return {
      appInfo: {},
      gif: gif + '?' + +new Date(),
    }
  },
  methods: {
    getInfo: () => {
      let appInfo = {}
      appInfo.appName = navigator.appName; //
      appInfo.appVersion = navigator.appVersion; //
      appInfo.platform = navigator.platform; // Win16(windows3.x)
      appInfo.userAgent = navigator.userAgent; //appCodeName,appName,appVersion,language,platform
      return appInfo
    }
  },
  mounted() {
    this.appInfo = this.getInfo()
  }
}
</script>

<style lang="less" scoped>
  @import "../../assets/less/mixin";
  li {
    height: 36px;
    line-height: 36px;
    font-size: 14px;
    color: #f60;
    padding: 0 20px;
    cursor: pointer;
    position: relative;
    box-sizing: border-box;
    white-space: nowrap;
  }
  .gif {
    margin-left: 300px;
    margin-top: 100px;
    &.rotate {
      .rotate(180deg)
    }
  }
</style>



