<template>
  <div class="tools-wrapper">
    <h2>Html</h2>
    <ul class="tips">
      <li>html</li>
      <li>html</li>
    </ul>
    <el-input
      ref="htmlInput"
      type="textarea"
      :autosize="{ minRows: 4, maxRows: 20}"
      placeholder=""
      v-model="textarea_html">
    </el-input>
    <div class="btn-wrap">
      <el-button @click="handle2Str"></el-button>
      <el-button @click="handle2Str2"></el-button>
      <el-button @click="handleReset"></el-button>
    </div>
    <el-input
      readonly
      type="textarea"
      :autosize="{ minRows: 4, maxRows: 20}"
      placeholder=""
      v-model="textarea_str">
    </el-input>
  </div>
</template>

<script>
let defaultHtml = `<p>HTML</p>
<p>HTMLHTMLJavaScript</p>
<p>HTML</p>
<p></p>`
export default {
  name: 'Html2str',
  data() {
    return {
      textarea_html: defaultHtml,
      textarea_str: '',
    }
  },
  methods: {
    handle2Str: function() {
      let htmlArr = this.textarea_html.replace(/\\/g, "\\\\").replace(/\\/g, "\\/").replace(/\'/g, "\"").replace(/\"/g, "\"").split('\n')
      let len = htmlArr.length
      let outArr = []
      htmlArr.map( (v, i) => {
        if (v !== "") {
          i === len -1 ? outArr.push("\'" + v + "\';") : outArr.push("\'" + v + "\'+\n")
        }
      })
      this.textarea_str = outArr.join("")
    },
    handle2Str2: function() {
      let htmlArr = this.textarea_html.replace(/\\/g, "\\\\").replace(/\\/g, "\\/").replace(/\'/g, "\'").replace(/\"/g, "\'").split('\n')
      let len = htmlArr.length
      let outArr = []
      htmlArr.map( (v, i) => {
        if (v !== "") {
          i === len -1 ? outArr.push("\"" + v + "\";") : outArr.push("\"" + v + "\"+\n")
        }
      })
      this.textarea_str = outArr.join("")
    },
    handleReset: function() {
      this.textarea_html = defaultHtml;
    },
  },
  watch: {
    textarea_html: function(value) {
      let htmlArr = value.replace(/\\/g, "\\\\").replace(/\\/g, "\\/").replace(/\'/g, "\"").replace(/\"/g, "\"").split('\n')
      let len = htmlArr.length
      let outArr = []
      htmlArr.map( (v, i) => {
        if (v !== "") {
          i === len -1 ? outArr.push("\'" + v + "\';") : outArr.push("\'" + v + "\'+\n")
        }
      })
      this.textarea_str = outArr.join("")
    }
  }
}
</script>

<style lang="less">
  .tools-wrapper {
    .tips {
      margin-bottom: 20px;
    }
    .el-textarea {
      width: 70%;
      textarea {
        color: #ff4949;
      }
    }
    .btn-wrap {
      margin: 20px;
    }
  }
</style>



